export default {
  data () {
    return {
      content: '',
      pageShow: false
    }
  },

  onLoad () {
    this.getRule()
  },

  methods: {
    getRule () {
      this.$loading()
      this.$http.getFightGroupRule().then(res => {
        if (res.status === 200) {
          this.content = res.data.content
          setTimeout(() => {
            this.pageShow = true
          }, 300)
        } else {
          this.$toast(res.msg)
        }
      })
    }
  },
}
